export default {
	components: {},
	props: {
		value: [Number, String, Object],
		list: {
			type: Array,
			default: () => {
				return []
			}
		},
		title: [Number, String]
	},
	data() {
		return {
			showList: "",
			showClass: '',
			selectItem: {},
			contentTop: 0
		}
	},
	watch: {
		list: {
			deep: true,
			handler(val) {
				this.reserData()
			}
		}
	},
	mounted() {
		this.showList = this.active;
		this.reserData()
	},
	methods: {
		reserData() {
			this.list.forEach(item => {
				if (item.value == this.value) {
					this.selectItem = item;
				}
			})
		},
		choose(item) {
			this.selectItem = item
			this.$emit('input', item.value)
			this.closePopup()
		},
		changePopup() {
			if (this.showList) {
				this.closePopup()
			} else {
				this.openPopup()
			}
		},
		openPopup() {
			// this.$parent  -> dropdown-menu
			this.$parent.$emit('close')
			this.showList = true
			this.$nextTick(() => {
				this.getElementData('.dropdown-item__selected', (data) => {
					this.contentTop = data[0].bottom
					this.showClass = 'show'
				})
			})
		},
		closePopup() {
			this.showClass = ''
			setTimeout(() => {
				this.showList = false
			}, 300)
		},
		close() {
			this.showClass = ''
			this.showList = false
		},
		getElementData(el, callback) {
			uni.createSelectorQuery().in(this).selectAll(el).boundingClientRect().exec((data) => {
				callback(data[0]);
			});
		}
	}
}
